.header {
	position: fixed;
	height: 90px;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 1;
	transition: 0.3s ease-in;
	overflow: hidden;
	background-color: var(--bg);
}

.header .navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 1240px;
	margin: auto;
	height: 100%;
	padding: 0 1rem;
}

.header .nav-menu a {
	color: var(--text-color);
}

.header {
	.navbar {
		.logo {
			height: 70px;
			width: 70px;
			align-items: center;
			padding: 0.1rem;
			img {
				width: 100%;
				height: 100%;
			}
		}
	}
}

.header .nav-menu {
	display: flex;
}

.header .nav-item {
	padding: 1rem;
	font-weight: 500;
}

.header .nav-item a:hover {
	padding-bottom: 12px;
	border-bottom: 3px solid var(--secondary-color);
}

.hamburger {
	display: none;
}

@media screen and (max-width: 940px) {
	.header {
		max-width: 100%;
		background-color: var(--bg);
	}

	.header .navbar {
		max-width: 100%;
	}

	.hamburger {
		display: block;
	}

	.dropdown__menu {
		display: none;
	}

	.nav-menu {
		position: fixed;
		left: -100%;
		top: 90px;
		flex-direction: column;
		background-color: var(--bg);
		width: 100%;
		height: 90vh;
		z-index: 999;
		text-align: center;
		transition: 0.3s;
	}

	.nav-menu.active {
		left: 0;
	}

	.nav-item {
		margin: 1.5rem 0;
	}

	.header .navbar img {
		width: 150px;
	}
}
